
import { defineComponent } from "vue";
import {
  IonList,
  IonItem,
  IonButton,
  IonGrid,
  IonFooter,
  IonToolbar,
  IonHeader,
  IonTitle,
  modalController 
} from "@ionic/vue"; 

export default defineComponent({
  components: { 
    IonGrid, 
    IonList,
    IonItem, 
    IonFooter,
    IonButton,
    IonToolbar,
    IonHeader,
    IonTitle,
  },
  props: {
    title: {
      type: String,
      default: 'Report Errors'
    },
    errors: {
      type: Array,
      required: true
    }
  },
  methods: {
    async closeModal() {
      await modalController.dismiss({})
    }
  }
})
